'use client';

import { PageMetaTags } from '@mintlify/models';
import NextHead from 'next/head';
import { useContext } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { BASE_PATH } from '@/env';
import type { FaviconsProps } from '@/types/favicons';

type HeadType = {
  favicons: FaviconsProps;
  metaTagsDict: PageMetaTags;
  ogImageUrl?: string;
};

export const Head = ({ favicons, metaTagsDict, ogImageUrl }: HeadType) => {
  const { docsConfig } = useContext(DocsConfigContext);

  if (docsConfig == null) {
    return null;
  }

  return (
    <NextHead>
      {favicons.icons.map((favicon) => (
        <link
          rel={favicon.rel}
          type={favicon.type}
          sizes={favicon.sizes}
          href={favicon.href}
          key={favicon.href}
        />
      ))}
      <meta name="msapplication-config" content={favicons.browserconfig} />
      <meta name="apple-mobile-web-app-title" content={docsConfig.name} />
      <meta name="application-name" content={docsConfig.name} />
      <meta name="theme-color" content="#ffffff" />
      <meta name="msapplication-TileColor" content={docsConfig.colors.primary} />
      <meta name="theme-color" content="#ffffff" />
      <meta name="generator" content="Mintlify" />
      <link rel="sitemap" type="application/xml" href={`${BASE_PATH}/sitemap.xml`} />
      {Object.entries(metaTagsDict).map(([key, value]) => (
        <meta key={key} name={key} content={value as never} />
      ))}
      {[
        { property: 'og:image', content: ogImageUrl },
        { property: 'og:image:width', content: '1200' },
        { property: 'og:image:height', content: '630' },
        { property: 'twitter:image', content: ogImageUrl },
        { property: 'twitter:card', content: 'summary_large_image' },
        { property: 'twitter:image:width', content: '1200' },
        { property: 'twitter:image:height', content: '630' },
      ].map(
        ({ property, content }) =>
          !metaTagsDict[property] && <meta key={property} property={property} content={content} />
      )}
      <title>{metaTagsDict['og:title'] as never}</title>
    </NextHead>
  );
};
