'use client';

import { SharedSessionAuth, UserInfo } from '@mintlify/validation';

import { USERINFO_LOCALSTORAGE_KEY, loadUserInfo, validateUserInfo } from './utils';

export async function handleSharedSessionRefresh(
  userAuth: SharedSessionAuth,
  callback: (userInfo: UserInfo) => void
) {
  const loadedUserInfo = loadUserInfo(userAuth);
  if (loadedUserInfo) {
    callback(loadedUserInfo);
    return;
  }

  localStorage.removeItem(USERINFO_LOCALSTORAGE_KEY);

  let data: unknown;
  try {
    const res = await fetch(userAuth.apiUrl, { credentials: 'include' });
    if (!res.ok) return;
    data = await res.json();
  } catch {
    return;
  }

  if (validateUserInfo(data)) {
    callback(data);
  }
}
