import { MintConfig } from '@mintlify/models';
import merge from 'ts-deepmerge';

type ThemeConfig = Omit<MintConfig, '$schema' | 'name' | 'favicon' | 'colors' | 'navigation'>;

const DefaultThemeConfig: ThemeConfig = {
  layout: 'topnav',
  sidebar: {
    items: 'container',
  },
  topbar: {
    style: 'default',
  },
  search: {
    location: 'top',
  },
  rounded: 'default',
  codeBlock: {
    mode: 'dark',
  },
  topbarCtaButton: {
    name: '',
    url: '',
    style: 'pill',
    arrow: true,
  },
};

const QuillThemeConfig: ThemeConfig = {
  layout: 'solidSidenav',
  sidebar: {
    items: 'border',
  },
  search: {
    location: 'side',
  },
  topbar: {
    style: 'default',
  },
  rounded: 'sharp',
  codeBlock: {
    mode: 'dark',
  },
  topbarCtaButton: {
    name: '',
    url: '',
    style: 'pill',
    arrow: true,
  },
};

const PrismThemeConfig: ThemeConfig = {
  layout: 'topnav',
  sidebar: {
    items: 'container',
  },
  search: {
    location: 'top',
  },
  topbar: {
    style: 'default',
  },
  rounded: 'default',
  codeBlock: {
    mode: 'dark',
  },
  background: {
    style: 'windows',
  },
  topbarCtaButton: {
    name: '',
    url: '',
    style: 'roundedRectangle',
    arrow: false,
  },
};

const VenusThemeConfig: ThemeConfig = {
  layout: 'sidenav',
  sidebar: {
    items: 'card',
  },
  search: {
    location: 'top',
  },
  topbar: {
    style: 'gradient',
  },
  rounded: 'default',
  codeBlock: {
    mode: 'dark',
  },
  topbarCtaButton: {
    name: '',
    url: '',
    style: 'roundedRectangle',
    arrow: false,
  },
};

export const applyLegacyThemeConfigs = (mintConfig: MintConfig): MintConfig => {
  let themeConfig: ThemeConfig;
  switch (mintConfig.theme) {
    case 'quill':
      themeConfig = QuillThemeConfig;
      break;
    case 'prism':
      themeConfig = PrismThemeConfig;
      break;
    case 'venus':
      themeConfig = VenusThemeConfig;
      break;
    default:
      themeConfig = DefaultThemeConfig;
      break;
  }

  return merge(themeConfig, mintConfig);
};
