'use client';

import { JwtAuth, UserInfo } from '@mintlify/validation';
import { NextRouter } from 'next/router';

import { USERINFO_LOCALSTORAGE_KEY, loadUserInfo, validateUserInfo } from './utils';

export async function handleJwtRefresh(
  userAuth: JwtAuth,
  router: NextRouter,
  callback: (userInfo: UserInfo) => void
) {
  const hash = location.hash.slice(1);
  const [jwt, anchor] = getJwtAndAnchor(hash);

  if (!jwt) {
    const loadedUserInfo = loadUserInfo(userAuth);
    if (loadedUserInfo) {
      callback(loadedUserInfo);
      return;
    }
    localStorage.removeItem(USERINFO_LOCALSTORAGE_KEY);
    return;
  }

  const { importSPKI, jwtVerify } = await import('jose');

  let data: unknown;
  for (const key of userAuth.signingKeys) {
    try {
      // we switched from ES256 to EdDSA - ES256 public keys (with padding) have length 178, EdDSA 113
      const alg = key.publicKey.length === 178 ? 'ES256' : 'EdDSA';
      const publicKey = await importSPKI(key.publicKey, alg);
      const { payload } = await jwtVerify(jwt, publicKey);
      for (const registeredClaim of ['aud', 'exp', 'iat', 'iss', 'jti', 'nbf', 'sub']) {
        delete payload[registeredClaim];
      }
      data = payload;
      break;
    } catch (e) {
      console.error(e);
    }
  }

  if (validateUserInfo(data)) {
    callback(data);
  }

  const newHash = anchor ? `#${anchor}` : '';
  // regardless of jwt success, we still want to remove the jwt from the url
  await router.replace(`${location.pathname}${location.search}${newHash}`);
}

// simple heuristic to determine if a string is JWT-ish enough to test - all JWTs start with 'ey' and contain exactly two '.'
function isJwtIsh(jwt: string) {
  return jwt.startsWith('ey') && jwt.match(/\./g)?.length === 2;
}

function getJwtAndAnchor(hash: string) {
  if (isJwtIsh(hash)) {
    return [hash, null];
  }

  const hashParams = new URLSearchParams(hash);
  const jwtParam = hashParams.get('jwt');
  if (jwtParam && isJwtIsh(jwtParam)) {
    return [jwtParam, hashParams.get('anchor')];
  }

  return [null, null];
}
