import { MintConfig } from '@mintlify/models';
import { DocsConfig } from '@mintlify/validation';

import { SidebarItemStyle } from '@/themes/mint/MainContentLayout/Nav/utils';

import { applyLegacyThemeConfigs } from './applyLegacyThemeConfigs';

export type LegacyThemeSettings = {
  isSidePrimaryNav: boolean;
  isSolidSidenav: boolean;
  isTopbarGradient: boolean;
  isSearchAtSidebar: boolean;
  shouldUseTabsInTopNav: boolean;
  sidebarStyle: SidebarItemStyle | undefined;
  rounded: 'default' | 'sharp' | undefined;
};

export function getLegacyThemeSettings(
  docsConfig: DocsConfig,
  mintConfig?: MintConfig
): LegacyThemeSettings {
  if (docsConfig.theme === 'mint' && !mintConfig) {
    mintConfig = applyLegacyThemeConfigs({
      $schema: '',
      name: '',
      favicon: '',
      colors: { primary: '#2AB673' },
      navigation: [],
    });
  }
  return {
    isSidePrimaryNav: mintConfig?.layout === 'sidenav' || mintConfig?.layout === 'solidSidenav',
    isSolidSidenav: mintConfig?.layout === 'solidSidenav',
    isTopbarGradient: mintConfig?.topbar?.style === 'gradient',
    isSearchAtSidebar: mintConfig?.search?.location === 'side',
    shouldUseTabsInTopNav:
      (mintConfig?.layout === 'sidenav' || mintConfig?.layout === 'solidSidenav') &&
      mintConfig.search?.location === 'side',
    sidebarStyle: mintConfig?.sidebar?.items,
    rounded: mintConfig?.rounded,
  };
}
