'use client';

import { UserAuth, UserInfo } from '@mintlify/validation';
import { NextRouter } from 'next/router';
import { useEffect, useState } from 'react';

import { storeEncrypted } from '@/utils/storeEncrypted';

import { NEXT_PUBLIC } from '../../env';
import { handleJwtRefresh } from './handleJwtRefresh';
import { handleOAuthRefresh } from './handleOAuthRefresh';
import { handleSharedSessionRefresh } from './handleSharedSessionRefresh';
import { StoredUserInfo, USERINFO_LOCALSTORAGE_KEY } from './utils';

export function useUserInfo(userAuth: UserAuth | undefined, router: NextRouter) {
  const [userInfo, setUserInfo] = useState<UserInfo>();
  const [isFetchingUserInfo, setIsFetchingUserInfo] = useState(false);

  useEffect(() => {
    const callback = (userInfo: UserInfo) => {
      setUserInfo(userInfo);
      const infoWithMetadata: StoredUserInfo = {
        retrievedAt: Date.now(),
        data: userInfo,
      };
      storeEncrypted(USERINFO_LOCALSTORAGE_KEY, infoWithMetadata);
    };
    const fetchAuthUserInfo = async () => {
      try {
        setIsFetchingUserInfo(true);
        const res = await fetch('/api/user');
        const data = await res.json();
        if (data.user != null) {
          callback(data.user);
        }
      } catch {
      } finally {
        setIsFetchingUserInfo(false);
      }
    };
    if (NEXT_PUBLIC.AUTH_ENABLED) {
      void fetchAuthUserInfo();
    }

    switch (userAuth?.type) {
      case 'shared-session': {
        void handleSharedSessionRefresh(userAuth, callback);
        return;
      }
      case 'jwt': {
        void handleJwtRefresh(userAuth, router, callback);
        return;
      }
      case 'oauth': {
        void handleOAuthRefresh(userAuth, router, callback);
        return;
      }
      case undefined: {
        localStorage.removeItem(USERINFO_LOCALSTORAGE_KEY);
        return;
      }
    }
    // should only attempt to refresh page data on page load
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return { userInfo, isFetchingUserInfo };
}
