import { UserAuth, UserInfo } from '@mintlify/validation';

import { DAY_IN_MILLISECONDS, TWO_WEEKS_IN_MILLISECONDS } from '@/constants';
import { loadEncrypted } from '@/utils/storeEncrypted';

export const USERINFO_LOCALSTORAGE_KEY = 'mintlify-user-info';

export type StoredUserInfo = {
  retrievedAt: number;
  data: UserInfo;
};

export function loadUserInfo(userAuth: UserAuth | undefined): UserInfo | null {
  // check if userAuth is enabled
  if (!userAuth) return null;

  const userInfo = loadEncrypted(USERINFO_LOCALSTORAGE_KEY, validateStoredUserInfo);
  if (!userInfo) return null;

  // check expiration
  const defaultExpiration =
    userAuth.type === 'shared-session' ? DAY_IN_MILLISECONDS : TWO_WEEKS_IN_MILLISECONDS;

  const expiresAt = userInfo.data.expiresAt
    ? userInfo.data.expiresAt * 1000 // user-provided dates are in seconds since epoch
    : userInfo.retrievedAt + defaultExpiration;
  if (Date.now() > expiresAt) return null;

  // check invalidation
  if (userAuth.invalidatedAt && userAuth.invalidatedAt > userInfo.retrievedAt) return null;

  return userInfo.data;
}

export function validateUserInfo(value: unknown): value is UserInfo {
  return (
    !!value &&
    typeof value === 'object' &&
    (!('expiresAt' in value) || typeof value.expiresAt === 'number') &&
    (!('groups' in value) ||
      (Array.isArray(value.groups) && value.groups.every((el) => typeof el === 'string'))) &&
    (!('content' in value) || (!!value.content && typeof value.content === 'object')) &&
    (!('apiPlaygroundInputs' in value) ||
      (!!value.apiPlaygroundInputs &&
        typeof value.apiPlaygroundInputs === 'object' &&
        (!('header' in value.apiPlaygroundInputs) ||
          (!!value.apiPlaygroundInputs.header &&
            typeof value.apiPlaygroundInputs.header === 'object')) &&
        (!('cookie' in value.apiPlaygroundInputs) ||
          (!!value.apiPlaygroundInputs.cookie &&
            typeof value.apiPlaygroundInputs.cookie === 'object')) &&
        (!('query' in value.apiPlaygroundInputs) ||
          (!!value.apiPlaygroundInputs.query &&
            typeof value.apiPlaygroundInputs.query === 'object')) &&
        (!('server' in value.apiPlaygroundInputs) ||
          (!!value.apiPlaygroundInputs.server &&
            typeof value.apiPlaygroundInputs.server === 'object'))))
  );
}

export function validateStoredUserInfo(value: unknown): value is StoredUserInfo {
  return (
    !!value &&
    typeof value === 'object' &&
    'retrievedAt' in value &&
    typeof value.retrievedAt === 'number' &&
    'data' in value &&
    validateUserInfo(value.data)
  );
}
