'use client';

import { useRouter } from 'next/router';
import Script from 'next/script';
import { useEffect } from 'react';

import { MdxPanel } from '@/components/MdxPanel';
import {
  ApiReferenceContext,
  AuthContext,
  DeploymentMetadataContext,
  DocsConfigContext,
  PageContext,
} from '@/contexts/ConfigContext';
import { LocalStorageContextController } from '@/contexts/LocalStorageContext';
import { MintlifyRouterContext } from '@/contexts/MintlifyRouterContext';
import { NavigationContextController } from '@/contexts/NavigationContext';
import FontScript from '@/fonts/FontScript';
import { useUserInfo } from '@/hooks/useUserInfo';
import { PageProps } from '@/types/page';
import { ColorVariables } from '@/ui/ColorVariables';
import { RoundedVariables } from '@/ui/RoundedVariables';
import { getAllMetaTags } from '@/utils/getAllMetaTags';
import { getLegacyThemeSettings } from '@/utils/getLegacyThemeSettings';

import { Api } from './Content/Api';
import { Content } from './Content/Content';
import { PageHeader } from './Content/PageHeader';
import ErrorBoundary from './ErrorBoundary';
import { Fonts } from './Fonts';
import { Head } from './Head';
import { MdxContent } from './MdxContent';

export default function PDFPage({
  cssFiles,
  description,
  entitlements,
  favicons,
  feedback,
  inkeep,
  jsFiles,
  mdxExtracts,
  mdxSource,
  mdxSourceWithNoJs,
  pageData,
  subdomain,
  // TODO: Deprecate this in favor of subdomain
  actualSubdomain,
  trieve,
  userAuth,
  auth,
  gitSource,
}: PageProps) {
  const router = useRouter();

  const { pageMetadata, apiReferenceData, docsConfig, docsNavWithMetadata, mintConfig } = pageData;
  const { userInfo, isFetchingUserInfo } = useUserInfo(userAuth, router);

  const metaTagsDict = getAllMetaTags(pageMetadata, docsConfig, actualSubdomain);

  useEffect(() => {
    enableLightMode();
  }, []);

  const enableLightMode = () => {
    document.documentElement.classList.remove('dark');
    window.localStorage.isDarkMode = false;
  };

  return (
    <MintlifyRouterContext.Provider value={router}>
      <AuthContext.Provider value={{ userAuth, userInfo, auth, isFetchingUserInfo }}>
        <DeploymentMetadataContext.Provider
          value={{
            subdomain,
            actualSubdomain,
            gitSource,
            inkeep,
            trieve,
            feedback,
            entitlements,
          }}
        >
          <ApiReferenceContext.Provider value={{ apiReferenceData }}>
            <DocsConfigContext.Provider
              value={{
                mintConfig,
                docsConfig,
                docsNavWithMetadata,
                legacyThemeSettings: getLegacyThemeSettings(docsConfig, mintConfig),
              }}
            >
              <PageContext.Provider
                value={{
                  pageMetadata,
                  pageType: 'pdf',
                  mdxExtracts,
                  description,
                }}
              >
                <NavigationContextController>
                  <FontScript />
                  <ColorVariables />
                  <RoundedVariables />
                  <Head favicons={favicons} metaTagsDict={metaTagsDict} />
                  <meta name="robots" content="noindex" />
                  {cssFiles.map(({ content }, i) => (
                    <style
                      key={i}
                      dangerouslySetInnerHTML={{
                        __html: content,
                      }}
                    />
                  ))}
                  {jsFiles.map(({ content }, i) => (
                    <Script id={i.toString()} key={i}>
                      {content}
                    </Script>
                  ))}
                  <Fonts>
                    {/* 
                      - max-w-4xl -> 896px -> 23.7cm
                      - width from all page format options is less than this amount so it should be good for all
                      - 832px is approximately 22cm
                    */}
                    <div id="content-area" className="mx-auto max-w-[832px]">
                      <LocalStorageContextController>
                        <PageHeader />
                        <MdxPanel mobile />
                        <Api />
                        <Content>
                          <ErrorBoundary>
                            <MdxContent
                              mdxSource={mdxSource}
                              mdxSourceWithNoJs={mdxSourceWithNoJs}
                              pageMetadata={pageMetadata}
                              userInfo={userInfo}
                            />
                          </ErrorBoundary>
                        </Content>
                      </LocalStorageContextController>
                    </div>
                  </Fonts>
                </NavigationContextController>
              </PageContext.Provider>
            </DocsConfigContext.Provider>
          </ApiReferenceContext.Provider>
        </DeploymentMetadataContext.Provider>
      </AuthContext.Provider>
    </MintlifyRouterContext.Provider>
  );
}
