'use client';

import { OAuth, UserInfo } from '@mintlify/validation';
import cookies from 'js-cookie';
import { NextRouter } from 'next/router';

import { CODE_VERIFIER_COOKIE } from './initiatePkceAndRedirect';
import { USERINFO_LOCALSTORAGE_KEY, loadUserInfo, validateUserInfo } from './utils';

export async function handleOAuthRefresh(
  userAuth: OAuth,
  router: NextRouter,
  callback: (userInfo: UserInfo) => void
) {
  const currentUrl = new URL(window.location.href);
  const code = currentUrl.searchParams.get('code');
  if (!code) {
    const loadedUserInfo = loadUserInfo(userAuth);
    if (loadedUserInfo) {
      callback(loadedUserInfo);
      return;
    }
    localStorage.removeItem(USERINFO_LOCALSTORAGE_KEY);
    return;
  }

  const data = await fetchOAuthUserInfo(userAuth, code);

  if (validateUserInfo(data)) {
    callback(data);
  }

  // regardless of oauth success, we still want to remove the code from the url
  currentUrl.search = '';
  await router.push(currentUrl);
}

async function fetchOAuthUserInfo(userAuth: OAuth, code: string) {
  const codeVerifier = cookies.get(CODE_VERIFIER_COOKIE);
  if (!codeVerifier) {
    console.error('missing code verifier');
    return;
  }

  let accessToken;
  try {
    accessToken = await exchangeCodeForToken(userAuth, code, codeVerifier);
  } catch (e) {
    console.error(`unable to complete oauth exchange request: ${e}`);
    return;
  }

  let data;
  try {
    const apiRes = await fetch(userAuth.apiUrl, {
      headers: { Authorization: `Bearer ${accessToken}` },
    });
    data = await apiRes.json();
  } catch (e) {
    console.error(`unable to complete oauth api request: ${e}`);
    return;
  }

  return data;
}

async function exchangeCodeForToken(userAuth: OAuth, code: string, codeVerifier: string) {
  const tokenUrl = new URL(userAuth.tokenUrl);
  tokenUrl.searchParams.append('grant_type', 'authorization_code');
  tokenUrl.searchParams.append('client_id', userAuth.clientId);
  tokenUrl.searchParams.append('redirect_uri', window.location.origin);
  tokenUrl.searchParams.append('code', code);
  tokenUrl.searchParams.append('code_verifier', codeVerifier);

  const exchangeRes = await fetch(tokenUrl, { method: 'POST' });
  const { access_token: accessToken } = await exchangeRes.json();
  if (typeof accessToken !== 'string') {
    throw Error('unable to parse access_token from oauth exchange response');
  }
  return accessToken;
}
